
  ---
title: 
---
<template>
  <my-wrapper title="">
    <template v-slot:extra>
      
    </template>
    <my-container>
      <my-filter label-width="80px" collapsible :model="defaultQuery" @submit="handleQuery">
        <my-input name="q1" label=""></my-input>
        <my-range name="q2" label="" mode="date" :props="{valueFormat:'yyyy-MM-dd'}"></my-range>
        <my-select name="q3" label="" collapsible :loader="optionsLoader"></my-select>
        <my-select name="q4" label="" collapsible :loader="optionsLoader"></my-select>
        <my-select name="q5" label="" collapsible :loader="optionsLoader"></my-select>
      </my-filter>
    </my-container>
    <my-container>
      <div class="toolbar">
        <el-button size="small" type="primary" icon="el-icon-plus"></el-button>
        <el-button size="small" icon="el-icon-upload2"></el-button>
        <el-button size="small" icon="el-icon-download"></el-button>
        <el-button v-show="selection.length" size="small"></el-button>
        <el-button v-show="selection.length" size="small"></el-button>
      </div>
      <my-container class="selection-tip" theme="info" :body-style="{padding:'0 10px'}">
        
        <my-text strong type="primary">{{selection.length}}</my-text>
        
        <el-button type="text" @click="clearSelection"></el-button>
      </my-container>

      <my-table ref="table"
                stripe
                column-filter
                :columns="tableColumns"
                :pagination="{layout:'total,sizes, ->, prev, pager, next'}"
                size="medium"
                :loader="loader"
                @selection-change="handleSelectionChange">

        <template v-slot:sex="{row}">
          <el-tag v-if="row.sex" size="small" type="primary"></el-tag>
          <el-tag v-else size="small" type="success"></el-tag>
        </template>

        <template v-slot:handle>
          <el-link type="primary"></el-link>
          <el-divider direction="vertical"></el-divider>
          <el-link type="primary"></el-link>
        </template>

      </my-table>
    </my-container>

  </my-wrapper>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,Divider as __Divider__,Link as __Link__,Tag as __Tag__} from 'element-ui';
     import {MyContainer as __MyContainer__,MyFilter as __MyFilter__,MyInput as __MyInput__,MyRange as __MyRange__,MySelect as __MySelect__,MyTable as __MyTable__,MyText as __MyText__,MyWrapper as __MyWrapper__} from '$ui';
     
     
     
     import MockForExample from '$my/code/mixin/mock-for-example'

  export default {
    mixins: [MockForExample],
    data() {
      return {
        columns: [],
        defaultQuery: this.$route.query,
        query: {},
        selection: []
      }
    },
    computed: {
      tableColumns() {
        const columns = this.columns.map((col, index) => {
          return {
            ...col,
            showOverflowTooltip: true,
            display: index < 4 ? 'always' : false
          }
        })
        columns.unshift(
          {
            type: 'selection'
          })

        columns.push({
          label: '',
          type: 'handle',
          prop: 'handle',
          width: '120px'
        })
        return columns
      }
    },
    methods: {
      optionsLoader() {
        return Promise.resolve([
          {label: '', value: 1},
          {label: '', value: 2}
        ])
      },
      loader(page, limit) {
        return this.fetchMockForExample({
          data: {
            ...this.query,
            page,
            limit
          }
        })
      },
      handleQuery(model) {
        this.query = model
        this.$refs.table.refresh(1)
      },
      handleSelectionChange(selection) {
        this.selection = selection
      },
      clearSelection() {
        this.selection = []
        this.$refs.table.clearSelection()
      }
    },
    created() {
      this.getColumns().then(res => {
        this.columns = res
      })
    }
  }
     __vue__.use(__Button__);
__vue__.use(__Divider__);
__vue__.use(__Link__);
__vue__.use(__Tag__)
     __vue__.use(__MyContainer__);
__vue__.use(__MyFilter__);
__vue__.use(__MyInput__);
__vue__.use(__MyRange__);
__vue__.use(__MySelect__);
__vue__.use(__MyTable__);
__vue__.use(__MyText__);
__vue__.use(__MyWrapper__)
    
  </script>

<style lang="scss" scoped>
  .toolbar, .selection-tip {
    margin-bottom: 14px;
  }
</style>

  